* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    font-size: 1.3rem;
    padding: 10px;
    width: 50%;
    margin: 10px;
    border-radius: .5rem;
    border: .5px solid grey;
}

button {
    background-color: #6d8da3;
    border: 0;
    font-size: 1.2rem;
    padding: 10px;
    border-radius: .5rem;
    font-weight: bolder;
}

strong.text-danger {
    color: red;
}

.cards-grid {
    display: flex;
    flex-flow: wrap;
}

.card {
    align-content: center;
    border: .5px solid grey;
    border-radius: .5rem;
    overflow: hidden;
    margin: 10px;
    width: 15%;
    max-height: 300px;
    text-align: center;
}

.card h3 {
    border-top: .5px solid grey;
    padding: 10px 0;
}

.card img {
    max-height: 150px;
    width: 100%;
    display: block;
}